.footer {
  width: 100%;

  &__content {
    position: sticky;
    background-attachment: fixed;
    z-index: -1;
    width: 100%;
    height: 145px;
    background-image: url('/images/bg-main/footer-img-0-75x.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @include tablet {
      height: 325px;
      background-image: url('/images/bg-main/footer-img-1x.png');
    }

    @include desktop {
      height: 555px;
      background-image: url('/images/bg-main/footer-img-1-5x.png');
    }
  }
}
