.move {
  &__content {
    @include tablet {
      display: flex;
      justify-content: space-between;
    }
  }

  &__title {
    font-size: 32px;
    line-height: 32px;
    text-align: left;
    margin-top: 60px;
    margin-bottom: 32px;

    @include tablet {
      font-size: 48px;
      margin-top: 120px;
    }

    @include desktop {
      font-size: 64px;
      line-height: 64px;
    }
  }

  &__text {
    font-size: 23px;
    line-height: 32px;
    text-align: left;

    @include tablet {
      display: block;
      width: 288px;
      margin-top: 120px;
    }

    @include desktop {
      width: 498px;
    }
  }
}
