.contacts {
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
  }

  &__title {
    font-size: 32px;
    line-height: 32px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 48px;

    @include tablet {
      font-size: 48px;
      line-height: 48px;
      font-weight: 500;
    }
  }

  &__container-form {
    display: flex;
    flex-direction: column;
    align-items: center;

    &-items {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    &-input {
      width: 278px;
      height: 48px;
      padding-left: 16px;
      border: none;
      border-radius: 40px;
      color: $silver-clr;
      margin-bottom: 16px;

      @include tablet {
        width: 288px;
      }

      @include desktop {
        width: 411px;
      }

      &:focus {
        box-shadow: none;
        outline: none;
      }
    }


    &-button {
      width: 278px;
      height: 56px;
      border-radius: 40px;
      border: none;
      border-color: $white-clr;
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
      color: $black-btn-clr;
      cursor: pointer;
      transition: all 0.5s ease, transform 0.3s ease;
      margin-top: 16px;
      margin-bottom: 48px;

      @include tablet {
        width: 288px;
      }

      @include desktop {
        width: 411px;
      }


      &:hover {
        box-shadow: 0 0 10px 0 $white-shadow;
      }
    }
    .tarea {
      width: 100%;
      height: 148px;
      resize: none;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      font-family: Popins, sans-serif;
      border-radius: 16px;
      padding-top: 14px;
      padding-left: 16px;
    }
  }

  .link {
    text-decoration: none;
    color: $white-clr;
    transition: color 0.5s ease;

    &:hover {
      color: $link-hov-clr;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    width: 237px;

    @include tablet {
      margin-bottom: auto;
      margin-left: 24px;
    }

    @include desktop {
      margin-left: 198px;
    }

    &-title {
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      margin-bottom: 8px;
    }

    &-text {
      font-size: 16px;
      line-height: 22px;
      font-weight: 500;
      margin-bottom: 24px;
    }
  }
}

